import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Message } from 'primeng/primeng';
import { ProductManageHttpService } from '../../../http/product-manage.http.service';
import { Commfunc } from "app/@uisftech/common/service/commonFun";


@Component({
    selector: 'app-relation-config-add',
    templateUrl: './relation-config-add.component.html',
    styleUrls: ['./relation-config-add.component.scss']
})
export class RelationConfigAddComponent implements OnInit {
    @Output() outValue: EventEmitter<any> = new EventEmitter();
    @Input() inValue: any;
    relation_vale: string; // 关系类型
    select_Fproduct: any = []; // 主控可售产品下拉
    select_Tproduct: any = []; // 关联可售产品下拉

    masterValue: any = {
        productName: '',
        productId: ''
    }; // 主控可售产品
    relationValue: any = {
        productName: '',
        productId: ''
    }; // 关联可售产品
    oneValue: string; // 主控可售产品input
    twoValue: string; // 关联可售产品input
    oneValueId: string; // 主控可售产品input
    twoValueId: string; // 关联可售产品input
    code: any;
    // 提示信息
    msgs: Message[] = [];
    // 关系类型
    relationType: any = [];
    constructor(private productManageHttpService: ProductManageHttpService, public commfunc: Commfunc) {
        this.codeValues(); // 调用方法，获取全部码值
        this.relationType = this.code['RelationType'];
    }

    ngOnInit() {
        // this.relationType = [
        //     { label: '请选择', value: '' },
        //     { label: '排斥', value: '1' },
        //     { label: '交叉', value: '2' },
        // ];
        this.select_Fproduct = [
            { label: '请选择', value: '' }
        ];
        // this.queryRelationType();
        this.product_query();
    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    // 可售产品下拉
    product_query() {
        const parm = {
            productTypeId: 'DIGITAL_GOOD',
            pageSize: 9999,
            pageNum: 1
        };
        this.productManageHttpService.findAvailableProductList(parm).subscribe(data => {
            if (data.returnCode.code === 'success') {
                data.resultList.forEach(item => {
                    const obj = {};
                    obj['label'] = item.productName;
                    obj['value'] = item.productId;
                    this.select_Fproduct.push(obj);
                });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务错误' });
        });
    }
    addChange() {
        const parms = {
            productId: this.relationValue.productId,
            productIdTo: this.masterValue.productId,
        };
        this.productManageHttpService.isExitProductAssoc(parms).subscribe(data => {
            if (data.returnCode.code === 'success') {
                const isExit = data.isExit;
                if (isExit === 'Y') {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: '该产品反向关系已存在！' });
                    return;
                } else {
                    this.oneValue = this.relationValue.productName;
                    this.twoValue = this.masterValue.productName;
                    this.oneValueId = this.relationValue.productId;
                    this.twoValueId = this.masterValue.productId;
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务错误' });
        });
    }
    addServe() {
        const parm0s = {
            productId: this.masterValue.productId,
            productIdTo: this.relationValue.productId
        };
        // 下拉框
        const parm1s = {
            productId: this.masterValue.productId,
            productIdTo: this.relationValue.productId,
            productAssocTypeId: this.relation_vale
        };
        // input
        const parm2s = {
            productId: this.oneValueId,
            productIdTo: this.twoValueId,
            productAssocTypeId: this.relation_vale
        };
        if (this.relation_vale === undefined || this.relation_vale === '') {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '关系类型不能为空!' });
            return;
        }
        if (this.masterValue.productId === undefined || this.masterValue.productId === '') {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '主控可售产品不能为空!' });
            return;
        }
        if (this.relationValue.productId === undefined || this.relationValue.productId === '') {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '关联可售产品不能为空!' });
            return;
        }
        if (this.masterValue.productId === this.relationValue.productId) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '主控可售产品不可与关联可售产品相同' });
            return;
        }
        if (this.oneValueId !== undefined && this.oneValueId !== '' && this.twoValueId !== undefined && this.twoValueId !== '') {
            if (this.oneValueId === this.twoValueId) {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '主控可售产品不可与关联可售产品相同' });
                return;
            }
        }
        this.productManageHttpService.isExitProductAssoc(parm0s).subscribe(data => {
            if (data.returnCode.code === 'success') {
                const isExit = data.isExit;
                if (isExit === 'Y') {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: '该产品关系已存在！' });
                    return;
                } else {
                    this.productManageHttpService.addProductAssoc(parm1s).subscribe(data1 => {
                        if (data1.returnCode.code === 'success') {
                            if (this.oneValueId !== undefined && this.oneValueId !== ''
                                && this.twoValueId !== undefined && this.twoValueId !== '') {
                                this.productManageHttpService.addProductAssoc(parm2s).subscribe(data2 => {
                                    if (data2.returnCode.code === 'success') {
                                        this.outValue.emit(true);
                                    } else {
                                        this.msgs = [];
                                        this.msgs.push({ severity: 'error', summary: '提示', detail: data2.errorMessage });
                                    }
                                }, error => {
                                    this.msgs = [];
                                    this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务错误' });
                                });
                            } else {
                                this.outValue.emit(true);
                            }
                        } else {
                            this.msgs = [];
                            this.msgs.push({ severity: 'error', summary: '提示', detail: data1.errorMessage });
                        }
                    }, error => {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务错误' });
                    });
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务错误' });
        });
    }
    close() {
        this.outValue.emit(false);
    }
    clearChange() {
        this.oneValue = '';
        this.twoValue = '';
        this.oneValueId = '';
        this.twoValueId = '';
    }
    masterValueC() {
        this.oneValue = '';
        this.twoValue = '';
        this.oneValueId = '';
        this.twoValueId = '';
    }
    relationValueC() {
        this.oneValue = '';
        this.twoValue = '';
        this.oneValueId = '';
        this.twoValueId = '';
    }
    // queryRelationType() {
    //     const param = {
    //         enumTypeId: 'prodRelationType'
    //     };
    //     this.productManageHttpService.getEnumInfoForXj(param).subscribe(data => {
    //         if (data.returnCode.code === 'success') {
    //             const relationType_list = data.enuMap.treelist;
    //             if (relationType_list.length > 0) {
    //                 relationType_list.forEach(item => {
    //                     const obj = {};
    //                     obj['label'] = item.label;
    //                     obj['value'] = item.value;
    //                     this.relationType.push(obj);
    //                 });
    //             }
    //         } else {
    //             this.msgs = [];
    //             this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
    //         }
    //     }, error => {
    //         this.msgs = [];
    //         this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    //     });
    // }
}
