import { Component, OnInit } from '@angular/core';
import { ConfirmationService } from 'primeng/primeng'; // 删除
import { Message } from 'primeng/primeng';
import { PrcBean } from './bean/product-relation-config.bean';
import { ProductManageHttpService } from '../../http/product-manage.http.service';
import { Commfunc } from "app/@uisftech/common/service/commonFun";

@Component({
    selector: 'app-product-relation-config',
    templateUrl: './product-relation-config.component.html',
    styleUrls: ['./product-relation-config.component.scss'],
    providers: [ProductManageHttpService, ConfirmationService]
})
export class ProductRelationConfigComponent implements OnInit {
    product_base_value: any;
    productId: any;
    // bean
    prcBean: PrcBean = new PrcBean();
    // 提示信息
    msgs: Message[] = [];
    // 关系类型
    relationType: any = [];
    AssocList: any = [];
    relation_vale: string; // 关系类型
    select_Fproduct: any = []; // 主控可售产品下拉
    // 总页数
    total: string;
    // 每页多少条
    pageSize = 10;
    pageNum = 1;
    first = 0; // 分页控制

    // 模态框
    showModel: string;
    display = false;
    detail = false;
    list: any;
    // 模态框的标题
    headerTitle: string;
    code: any;
    constructor(
        private productManageHttpService: ProductManageHttpService,
        private confirmationService: ConfirmationService,
        public commfunc: Commfunc,
    ) {
        this.codeValues(); // 调用方法，获取全部码值
        this.relationType = this.code['RelationType'];
    }

    ngOnInit() {
        // this.relationType = [
        //     { label: '请选择', value: '' },
        //     { label: '排斥', value: '1' },
        //     { label: '交叉', value: '2' },
        // ];
        this.select_Fproduct = [
            { label: '请选择', value: '' }
        ];
        // this.queryRelationType();
        this.product_query();
        this.productAssocList();
    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    // 重置
    resetClick() {
        this.relation_vale = '';
        this.productId = '';
        this.product_base_value = '';
        this.pageNum = 1;
    }

    // 默认查询
    productAssocList() {
        const param = {
            productId: this.productId,
            productAssocTypeId: this.relation_vale,
            productIdTo: this.product_base_value,
            pageSize: this.pageSize,
            pageNum: this.pageNum
        };
        this.productManageHttpService.queryProductAssocList(param).subscribe(data => {
            if (data.returnCode.code === 'success') {
                this.AssocList = data.productAssocList;
                this.total = data.total;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务错误' });
        });
    }

    basicDetails(item) {
        this.detail = true;
        this.headerTitle = '详情';
        this.list = item;
        let createrts = new Date(item.lastUpdatedStamp);
        this.list.lastUpdatedStamp = this.commfunc.transDateN(createrts);
        let created = new Date(item.createdStamp);
        this.list.createdStamp = this.commfunc.transDateN(created);
        
    }
    close() {
        this.detail = false;
    }
    // 新增
    addClick() {
        this.display = true;
        this.headerTitle = '新增关系';
        this.showModel = '1';
    }
    // 新增的回调
    addCall(param) {
        if (param) {
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: '提示', detail: '新增成功' });
        }
        this.display = false;
        this.first = 0;
        this.productAssocList();
    }
    // 删除
    deleteClick(item) {
        this.confirmationService.confirm({
            message: '确认要删除吗?',
            accept: () => {
                // 条件
                this.productManageHttpService.delProductAssoc(item).subscribe(data => {
                    if (data.returnCode.code === 'success') {
                        this.msgs = [];
                        this.msgs.push({ severity: 'success', summary: '提示', detail: '删除成功' });
                        this.productAssocList();
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
                    }
                }, error => {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: '删除失败' });
                });
            }
        });
    }
    // 分页
    paginate(event) {
        // 每页显示的条数
        this.pageSize = Number(event.rows);
        // 当前页
        const currentPage = event.page + 1;
        this.pageNum = currentPage;
        this.first = (currentPage - 1) * this.pageSize;
        // 调用查询的方法
        this.productAssocList();
        this.pageNum = 1;
    }
    // 主控产品下拉
    product_query() {
        const parm = {
            productTypeId: 'DIGITAL_GOOD',
            pageSize: 9999,
            pageNum: 1
        };
        this.productManageHttpService.findAvailableProductList(parm).subscribe(data => {
            if (data.returnCode.code === 'success') {
                data.resultList.forEach(item => {
                    const obj = {};
                    obj['label'] = item.productName;
                    obj['value'] = item.productId;
                    this.select_Fproduct.push(obj);
                });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务错误' });
        });
    }
    // queryRelationType() {
    //     const param = {
    //         enumTypeId: 'prodRelationType'
    //     };
    //     this.productManageHttpService.getEnumInfoForXj(param).subscribe(data => {
    //         if (data.Header.RetCode === '000000') {
    //             const relationType_list = data.Body.enuMap.treelist;
    //             if (relationType_list.length > 0) {
    //                 relationType_list.forEach(item => {
    //                     const obj = {};
    //                     obj['label'] = item.label;
    //                     obj['value'] = item.value;
    //                     this.relationType.push(obj);
    //                 });
    //             }
    //         } else {
    //             this.msgs = [];
    //             this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
    //         }
    //     }, error => {
    //         this.msgs = [];
    //         this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    //     });
    // }
}
